import React from 'react'
import { Link } from 'react-router-dom'
import './ProductItem.scss'
import FavouriteHeart from '../FavouriteHeart/FavouriteHeart'

export default function ProductItem({precio, imgUrl, nombre, cuotas, itemId, shipping, selectedAsFavourite, isFavourite}) {
    return (
        
        <div className="productItem">
            <FavouriteHeart isFavourite={isFavourite} itemId={itemId} onFavouriteClic={() => selectedAsFavourite(itemId, nombre)} />
            <Link className="productItem__link" to={`/product/${itemId}`}>
            <img className="productItem__img" src={imgUrl} alt=""/>
            <div className="productItem__separation">
                <hr className="productItem__line"/>
                {shipping ? 
                <div className="productItem__container-carrito">
                    <svg className="productItem__carrito" viewBox="0 0 39 32"><path d="M21.7 24q.6-1.3 2-2t2.6-.8q1.5 0 2.7.7t2 2h4.4v-7.5L30 10.3h-4.8V6.8h5.6q.5 0 .8.2t.6.5l6.2 7.4q.3 0 .4.3t0 .6v9.6q0 .7-.5 1.2t-1 .4h-6q-.4 1.8-1.8 3t-3.2 1-3.3-1-1.7-3h-6q-.4 1.8-1.8 3t-3.2 1-3.3-1-1.7-3H1.7Q1 27.4.5 27T0 25.6V4q0-1.7 1.2-2.8T4 0h17.2q1.6 0 2.8 1.2T25.2 4v14.3h-3.5V4q0-.2-.2-.4t-.3-.2H4q-.3 0-.4.2t-.2.4v20h2.3q.6-1.3 2-2t2.6-.8q1.5 0 2.7.7t2 2h6.7zm4.6 4q.7 0 1.2-.5t.5-1.2-.5-1.2-1.2-.3-1.2.5-.3 1.3.5 1.2 1.3.5zm-16 0q.7 0 1.2-.5t.5-1.2-.5-1.2-1.2-.3T9 25t-.4 1.3.5 1.2 1.4.5z"></path></svg>
                </div> : ''
                }
            </div>
            <h4 className="productItem__price">${precio}</h4>
            <h5 className="productItem__cuota">{cuotas.quantity}x $ {cuotas.amount} comodas cuotas.</h5>
            <div className="productItem__moreInfo">
                <p className="productItem_info">{nombre}</p>
            </div>
            </Link>
        </div>
        
    )
}
