import React from 'react';
import './NavBar.scss'
import logoml from './logo-ml.png'
import compraAhora from './compra-ahora.png'
import { Link } from 'react-router-dom';
import Dexie from 'dexie';


export default function NavBar({searchProduct}) {
    let countryId = localStorage.getItem('countryID');
    return (
        <div className="navBarContainer">
           <Link to={`/home/${countryId}`} ><img className="navBarContainer__logo" src={logoml} alt=""/></Link> 
            <div className="navBarContainer__searchContainer">
                <input placeholder="Buscar productos" className='navBarContainer__input' onChange={(e) => searchProduct(e)}/>
                <button className="navBarContainer__searchButton">
                    <svg fill="#c7c7c7" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 30 30" width="30px" height="30px"><path d="M 13 3 C 7.4889971 3 3 7.4889971 3 13 C 3 18.511003 7.4889971 23 13 23 C 15.396508 23 17.597385 22.148986 19.322266 20.736328 L 25.292969 26.707031 A 1.0001 1.0001 0 1 0 26.707031 25.292969 L 20.736328 19.322266 C 22.148986 17.597385 23 15.396508 23 13 C 23 7.4889971 18.511003 3 13 3 z M 13 5 C 17.430123 5 21 8.5698774 21 13 C 21 17.430123 17.430123 21 13 21 C 8.5698774 21 5 17.430123 5 13 C 5 8.5698774 8.5698774 5 13 5 z"/></svg>
                </button>
            </div>
            {/* <img className="navBarContainer__compraAhora" src={compraAhora} alt=""/> */}
            <div className=""></div>
        </div>
    )
}
